.top-tovarov.top-tovarov--nomain{
    .top-tovarov__wrap{
        margin-bottom: 0;
    }
}
.tovar{
    margin-bottom: 268px;
}
.tovar-img{
    width: 150px;
    height: 150px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
    transition: 0.5s all;
}
.tovar-img:hover{
    transform: scale(1.05);
}
.tovar-img2{
    width: 320px;
    height: 307px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    display: block;
    cursor: zoom-in;
    background-color: #fff;
}
.tovar__wrap{
    display: flex;
}
.tovar__left{
    width: 150px;
    margin-right: 5px;
    
    .swiper-container-left {
        height: 390px;
        padding-top: 35px;
        padding-bottom: 35px;
    }

    .swiper-button.swiper-button-prev{
        width: 100%;
        left: 0;
        top: 0;
        background-color: #fff;
    }
    .swiper-button.swiper-button-prev:after{
        transform: rotate(90deg) translateX(5px);
    }
    .swiper-button-disabled{
        opacity: 0;
    }

    .swiper-button.swiper-button-next{
        width: 100%;
        left: 0;
        bottom: 0;
        top: auto;
        background-color: #fff;
    }
    .swiper-button.swiper-button-next:after{
        transform: rotate(90deg) translateX(5px);
    }
}
.tovar__right{
    width: 320px;
    margin-top: 15px;
    .swiper-button{
        display: none;
    }
    .swiper-slide-active{
        z-index: 2;
    }
}


.swiper-button.swiper-button-next:after, .swiper-button.swiper-button-prev:after{
    color: rgba(31, 31, 31, 0.4);
    font-size: 20px;
    transition: 0.3s all;
}

.swiper-button:hover:after{
    color: rgba(31, 31, 31, 1);
}
.tovar__slider{
    display: flex;
    margin-right: 148px;
}

.tovar__info{
    max-width: 372px;
}
.tovar__info__top{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 18px;
}
.tovar__info__name{
    padding-right: 15px;
    font-weight: bold;
    font-size: 20px;
    line-height: 19px;
    color: #1F1F1F;
}
.tovar-like{
    position: relative;
    top: -2px;
    font-size: 0;
}
.tovar-like svg path{
    transition: 0.3s all;
}
.tovar-like:hover{
    svg path{
        stroke-opacity: 1;
    }
}

.tovar__info__sub{
    font-size: 16px;
    line-height: 18px;
    display: flex;
    align-items: center;
    letter-spacing: 0.02em;
    color: rgba(31, 31, 31, 0.6);
    margin-bottom: 13px;
}
.tovar__param__name{
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;
    letter-spacing: 0.02em;
    color: #1F1F1F;
    margin-bottom: 22px;
}
.nice-select{
    border-radius: 0;
    border: 1px solid rgba(31, 31, 31, 0.2);
    height: 28px;
    line-height: 28px;
    color: #1F1F1F;
    font-weight: bold;
    font-size: 14px;
    padding-left: 14px;
    min-width: 85px;
    margin-bottom: 11px;
    float: none;
    display: inline-block;
}
.nice-select .list{
    margin-top: 0;
    margin-bottom: 0;
    border-radius: 0;
}
.nice-select .nice-select-dropdown{
    border-radius: 0;
    min-width: 100%;
    border: 1px solid rgba(31, 31, 31, 0.2);
}
.nice-select:after{
    border-right: 1px solid #1F1F1F;
    border-bottom: 1px solid #1F1F1F;
    width: 6px;
    height: 6px;
    margin-top: -5px;
}
.choose-link{
    margin-bottom: 18px;
}
.choose-link a{
    letter-spacing: 0.02em;
    text-decoration-line: underline;
    font-size: 16px;
    line-height: 18px;
    color: rgba(31, 31, 31, 0.6);
    letter-spacing: 0.02em;
}

.count-block{
    width: 85px;
    height: 28px;
    position: relative;
    input{
        height: 28px;
        width: 100%;
        text-align: center;
        appearance: none;
        border: 0;
        padding: 0;
        border: 1px solid rgba(31, 31, 31, 0.2);
        padding-right: 20px;
        padding-left: 20px;
    }
    button{
        height: 28px;
        position: absolute;
        top: 0;
        width: 20px;
        background-color: transparent;
        border: 0;
        letter-spacing: 0.02em;
        color: rgba(31, 31, 31, 0.6);
        font-size: 14px;
        line-height: 18px;
        cursor: pointer;
        transition: 0.3s all;
    }
    button:hover{
        color: #1F1F1F;
    }
    .count-block__left{
        left: 0;
    }
    .count-block__right{
        right: 0;
    }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

