.tovar__tabs{
    display: flex;
    background: #1F1F1F;
    color: #fff;
    margin-bottom: 100px;
    transition: 0.3s all;
}

.tovar__tabs__wrap{
    max-width: 1440px;
    margin: 0 auto;
    width: 100%;
    padding-left: 16px;
    padding-right: 16px;
    display: flex;
    justify-content: center;
}
.tovar__tabs__col{
    flex: 1;
    display: flex;
    justify-content: center;
}

.tovar__tabs__container{
    display: none;
}
.tovar__tabs__nav{
    padding-top: 26px;
    padding-bottom: 22px;
    padding-left: 15px;
    font-size: 20px;
    line-height: 24px;
    font-family: var(--bebas);
    display: flex;
    padding-right: 30px;
    position: relative;
    cursor: pointer;
    transition: 0.3s all;
}
.tovar__tabs__nav:after{
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%) rotate(-135deg);
    margin-top: 5px;
    content: "";
    width: 6px;
    height: 6px;
    display: block;
    border-right: 1px solid #fff;
    border-bottom: 1px solid #fff;
    transition: 0.3s all;
}
.tovar__tabs__container{
    position: absolute;
        color: #1F1F1F;
        max-width: 305px;
        padding-left: 15px;
        padding-top: 21px;
        padding-bottom: 21px;
        padding-right: 15px;
        background-color: #fff;
        z-index: 2;
    p{
        font-size: 14px;
        line-height: 18px;
        /* or 129% */
        letter-spacing: 0.02em;
        color: rgba(31, 31, 31, 0.7);
        margin-top: 0;
        margin-bottom: 15px;
    }
    p.bold{
        font-weight: bold;
        color: #1F1F1F;
        margin-bottom: 8px;
    }
    p.line{
        margin-bottom: 7px;
    }
    a{
        font-weight: bold;
        font-size: 14px;
        line-height: 18px;
        letter-spacing: 0.02em;
        text-decoration-line: underline;
        color: #1F1F1F;
        transition: 0.3s all;
    }
    a:hover{
        opacity: 0.8;
    }
}
.tovar__tabs__item.active{
    .tovar__tabs__nav:after{
        margin-top: 0;
        transform: translateY(-50%) rotate(45deg);
    }
    .tovar__tabs__container{
        display: block;
        animation: fadein 0.3s;
        
    }
}
.tovar__tabs__nav:hover{
    opacity: 0.8;
}

