    }
}
.checkbox{
    display: flex;
    margin-bottom: 9px;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: 0.02em;
    transition: 0.3s all;
    color: rgba(31, 31, 31, 0.6);
    cursor: pointer;
    input{
        display: none
    }
    input + span{
        width: 18px;
        height: 18px;
        border: 1px solid rgba(31, 31, 31, 0.2);
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 12px;
        min-width: 18px;
    }
    input + span:before{
        content: "";
        width: 8px;
        height: 8px;
        opacity: 0;
        display: block;
        background-color: #1F1F1F;
    }
    input:checked + span:before{
        opacity: 1;
    }
}
.checkbox:hover{
