        align-items: center;
        flex: 1;
        padding-top: 5px;
        padding-bottom: 5px;
    }
    .filter-toggle span{
        font-size: 14px;
        line-height: 17px;
        color: #1F1F1F;
        display: flex;
        align-items: center;
        font-family: var(--bebas);
        padding-right: 19px;
        position: relative;
    }
    .filter-toggle span:after{
        content: "";
        width: 6px;
        height: 6px;
        display: block;
        border-radius: 0px;
        border-right: 1px solid #1F1F1F;
        border-bottom: 1px solid #1F1F1F;
        transform: rotate(45deg);
        position: absolute;
        right: 0;
        top: 2px;
        transition: 0.3s all;
    }
    .filter-toggle.active span:after{
        transform: rotate(225deg);
        top: 7px;
    }
    .catalog__sort{
        min-width: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .catalog__title{
        width: 100%;
        text-align: center;
        padding-right: 0;
        padding-bottom: 13px;
        border-bottom: 1px solid rgba(31, 31, 31, 0.2);
        margin-bottom: 6px;
    }
    .catalog__sort .nice-select{
        border: 0;
        margin-bottom: 0;
        min-width: initial;
    }
    .catalog__sort .nice-select .current{
        font-size: 14px;
        font-family: var(--bebas);
        position: relative;
    }

    .catalog__sort .nice-select .current:after{
        border-right: 1px solid #1F1F1F;
        border-bottom: 1px solid #1F1F1F;
        width: 6px;
        height: 6px;
        margin-left: 10px;
        content: "";
        display: block;
        margin-top: -4px;
        pointer-events: none;
        transform-origin: 66% 66%;
        transform: rotate(45deg);
        transition: all .15s ease-in-out;
    }
    .catalog__sort .nice-select:after{
        display: none;
    }

    .catalog__sort .nice-select.open {
        .current:after{
            transform: rotate(225deg);
            margin-top: 0px;
        }
        
    }   
    .filter{
        display: none;  
        width: 100%;
        max-width: 343px;
        margin: 0 auto; 
    }
    .filter.active{
        display: block;
        animation: fadein 0.3s;
    }
    .pagination{
        width: 100%;
        margin-top: 25px;
    }


    .catalog__list .tovar__item{
        width: calc(100% / 2 - 14px);
        margin-left: 7px;
        margin-right:7px;
    }
    .catalog__list{
        margin-left: -7px;
        margin-right: -7px;
    }
}